<template>
    <div class="phone">
        <a-breadcrumb class="bread">
            <a-breadcrumb-item>
                <a href="/customerService/pc">{{ $t("language.title") }}</a>
            </a-breadcrumb-item>
            <a-breadcrumb-item class="text">
                {{ $t("language.phone") }}
            </a-breadcrumb-item>
        </a-breadcrumb>
        <div class="content">
            <a-row class="rowItem">
                <a-col :span="8">
                    <p class="lable">{{ $t("language.phonecode") }}:</p>
                </a-col>
                <a-col :span="12">
                    <a-input
                        v-model="formState.phone"
                        :placeholder="$t('language.inputPhone')"
                    />
                </a-col>
            </a-row>
            <a-row class="rowItem">
                <a-col :span="8">
                    <p class="lable">{{ $t("language.problem") }}:</p>
                </a-col>
                <a-col :span="12">
                    <a-select
                        v-model="formState.value"
                        :placeholder="$t('language.problemse')"
                        ref="select"
                        @change="handleChange"
                        class="select"
                    >
                        <a-select-option
                            v-for="(item, i) of problemList"
                            :key="i"
                            :value="item"
                        >
                            {{ item }}
                        </a-select-option>
                    </a-select>
                </a-col>
            </a-row>
            <a-row class="rowItem btnTop">
                <a-col :span="8"> </a-col>
                <a-col :span="12" class="btnCol">
                    <a-button type="primary" class="btnNext" @click="onSubmit">
                        {{ $t("language.submit") }}
                    </a-button>
                </a-col>
            </a-row>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            formState: {},
            problemList: [],
            subimgBtn: true,
        };
    },
    created() {
        this.problemList = [
            this.$t("language.pro1"),
            this.$t("language.pro2"),
            this.$t("language.pro3"),
            this.$t("language.pro4"),
            this.$t("language.pro5"),
        ];
    },
    methods: {
        handleChange(e) {
            console.log(e);
            this.questionTypeName = e;
        },
        onSubmit() {
            if (!this.subimgBtn) {
                return;
            }
            if (!/^1[3456789]\d{9}$/.test(this.formState.phone)) {
                this.$message.error(this.$t("language.Malformed"));
                return;
            }
            let option = {
                phone: this.formState.phone,
                // questionTypeId:
                questionTypeName: this.questionTypeName,
            };
            this.subimgBtn = !this.subimgBtn;
            this.$http.post(this.$api.setPhoneHelp, option).then((res) => {
                this.subimgBtn = !this.subimgBtn;
                console.log(res);
                if (res.code == 0) {
                    this.$message.success(this.$t("language.successfully"));
                    this.$router.go(-1);
                }
            });
        },
    },
};
</script>

<style lang="scss" >
.phone {
    margin: 15px 18% 0;
    .content {
        background: #fff;
        margin: 20px 10%;
        padding: 70px;
        .lable {
            margin: 0;
            text-align: right;
            line-height: 44px;
            padding-right: 10px;
            color: #333;
            font-size: 16px;
        }
        .select {
            width: 100%;
            height: 44px;
        }
        .rowItem {
            margin-bottom: 20px;
            .btnCol {
                text-align: left;
                .btnNext {
                    background: var(--themeColor);
                    border: none;
                    padding-left: 35px;
                    padding-right: 35px;
                }
            }
        }
        .btnTop {
            margin-top: 70px;
        }
    }
    .ant-select-selection--single {
        height: 44px;
    }
    .ant-select-selection__rendered {
        line-height: 44px;
    }
}
</style>